import React from 'react';
import { cn } from '../../lib/utils';

// Table 容器
interface TableProps extends React.HTMLAttributes<HTMLTableElement> {
  variant?: 'default' | 'bordered' | 'striped';
}

export const Table = React.forwardRef<HTMLTableElement, TableProps>(
  ({ className, variant = 'default', ...props }, ref) => {
    const baseClasses = 'w-full border-collapse';
    const variantClasses = {
      default: '',
      bordered: 'border border-gray-200',
      striped: ''
    };

    return (
      <table
        ref={ref}
        className={cn(baseClasses, variantClasses[variant], className)}
        {...props}
      />
    );
  }
);
Table.displayName = 'Table';

// Table Header
interface TableHeaderProps extends React.HTMLAttributes<HTMLTableSectionElement> {}

export const TableHeader = React.forwardRef<HTMLTableSectionElement, TableHeaderProps>(
  ({ className, ...props }, ref) => (
    <thead ref={ref} className={cn('bg-gray-50', className)} {...props} />
  )
);
TableHeader.displayName = 'TableHeader';

// Table Body
interface TableBodyProps extends React.HTMLAttributes<HTMLTableSectionElement> {}

export const TableBody = React.forwardRef<HTMLTableSectionElement, TableBodyProps>(
  ({ className, ...props }, ref) => (
    <tbody ref={ref} className={cn('divide-y divide-gray-200', className)} {...props} />
  )
);
TableBody.displayName = 'TableBody';

// Table Row
interface TableRowProps extends React.HTMLAttributes<HTMLTableRowElement> {
  hover?: boolean;
  selected?: boolean;
}

export const TableRow = React.forwardRef<HTMLTableRowElement, TableRowProps>(
  ({ className, hover = true, selected = false, ...props }, ref) => {
    const baseClasses = 'transition-colors';
    const hoverClasses = hover ? 'hover:bg-gray-50' : '';
    const selectedClasses = selected ? 'bg-blue-50' : '';

    return (
      <tr
        ref={ref}
        className={cn(baseClasses, hoverClasses, selectedClasses, className)}
        {...props}
      />
    );
  }
);
TableRow.displayName = 'TableRow';

// Table Head Cell
interface TableHeadProps extends React.ThHTMLAttributes<HTMLTableCellElement> {
  sortable?: boolean;
  sortDirection?: 'asc' | 'desc' | null;
  onSort?: () => void;
}

export const TableHead = React.forwardRef<HTMLTableCellElement, TableHeadProps>(
  ({ className, sortable = false, sortDirection, onSort, children, ...props }, ref) => {
    const baseClasses = 'px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider';
    const sortableClasses = sortable ? 'cursor-pointer select-none' : '';

    return (
      <th
        ref={ref}
        className={cn(baseClasses, sortableClasses, className)}
        onClick={sortable ? onSort : undefined}
        {...props}
      >
        <div className="flex items-center gap-2">
          {children}
          {sortable && (
            <span className="text-gray-400">
              {sortDirection === 'asc' && '↑'}
              {sortDirection === 'desc' && '↓'}
              {!sortDirection && '↕'}
            </span>
          )}
        </div>
      </th>
    );
  }
);
TableHead.displayName = 'TableHead';

// Table Cell
interface TableCellProps extends React.TdHTMLAttributes<HTMLTableCellElement> {
  align?: 'left' | 'center' | 'right';
}

export const TableCell = React.forwardRef<HTMLTableCellElement, TableCellProps>(
  ({ className, align = 'left', ...props }, ref) => {
    const baseClasses = 'px-6 py-4 whitespace-nowrap text-sm text-gray-900';
    const alignClasses = {
      left: 'text-left',
      center: 'text-center',
      right: 'text-right'
    };

    return (
      <td
        ref={ref}
        className={cn(baseClasses, alignClasses[align], className)}
        {...props}
      />
    );
  }
);
TableCell.displayName = 'TableCell';

// Table Footer
interface TableFooterProps extends React.HTMLAttributes<HTMLTableSectionElement> {}

export const TableFooter = React.forwardRef<HTMLTableSectionElement, TableFooterProps>(
  ({ className, ...props }, ref) => (
    <tfoot ref={ref} className={cn('bg-gray-50', className)} {...props} />
  )
);
TableFooter.displayName = 'TableFooter';

// Table Caption
interface TableCaptionProps extends React.HTMLAttributes<HTMLTableCaptionElement> {}

export const TableCaption = React.forwardRef<HTMLTableCaptionElement, TableCaptionProps>(
  ({ className, ...props }, ref) => (
    <caption ref={ref} className={cn('mt-4 text-sm text-gray-500', className)} {...props} />
  )
);
TableCaption.displayName = 'TableCaption';

// 默认导出
export default Table;
